<template>
  <div class="home">
    <b-alert :show="alertCountdown" dismissible fade :variant="alertType" @dismiss-count-down="countDownChanged" >
      {{alertText}}
    </b-alert>
    <div class="homePage-layout">
      <div class="public-rooms">
        <div class="div-hdr">
          <h5><b>الغرف العامة</b></h5>
          <b-link href=""><span>عرض الكل</span></b-link>
        </div>
        <div class="text-center text-danger my-2" v-show="isLoading">
          <b-spinner class="align-middle"></b-spinner>
        </div>
        <div class="public-rooms-body" v-show="!isLoading">
          <b-row>
            <b-col lg="12" md="12" sm="12" v-if="publicRooms.length == 0">
              <div class="pt-5 no-content" style="text-align:center">
                <img src="../assets/images/data-not-found.png">
                <h4> لا يوجد غرف عامة</h4>
              </div>
            </b-col>
            <b-col lg="4" md="6" sm="6" v-else v-for="(room, index) in publicRooms" :key="index">
              <div>
                <b-card no-body class="overflow-hidden room-card" >
                  <b-row no-gutters>
                    <b-col md="5">
                      <div class="card-img">
                        <img :src="room.image" alt="Image" class="rounded-0">
                      </div>
                    </b-col>
                    <b-col md="7">
                      <b-card-body :title="room.name">
                        <b-card-text>
                          {{room.subject}}
                        </b-card-text>
                        <b-link href="#foo">
                        </b-link>
                      </b-card-body>
                    </b-col>
                  </b-row>
                </b-card>
              </div>
            </b-col>
          </b-row>
        </div>
      </div>
      <rooms :isHome="isHome"></rooms>
    </div>
 </div>
</template>

<script>
  import store from "../store/index.js"
  import rooms from "../components/Rooms.vue"
  import { sendRequest }  from "../utils/RequestHelper.js"

  export default {
    name: 'Home',
    data() {
      return {
        PublicRoomIsLoading: false,
        alertCountdown: 0,
        alertType: '',
        alertText: '',
        isHome: true,
        isLoading: false,
        publicRooms: []
      }
    },
    methods: {
      validateState(name) {
        const { $dirty, $error } = this.$v.form[name];
        return $dirty ? !$error : null;
      },
      countDownChanged(alertCountdown) {
        this.alertCountdown = alertCountdown
      },
      loadPublicRooms() {
        this.isLoading = true;
        var successCallback = (response) => {
          this.publicRooms = response.data.data.slice(0, 3);
          this.isLoading = false;
        };
        var errorCallback = () => {
          this.isLoading = false;
          this.alertType = "danger"
          this.alertText = "خطأ .. حاول مرة اخرى لاحقا"
          this.alertCountdown = process.env.VUE_APP_ALERT_COUNTDOWN;
        };
        sendRequest('get', '/teacher/show-public-rooms', true, null , successCallback, errorCallback);
      },
    },
    created() {
      if(store.getters.authToken == null) {
        store.commit("SET_TOKEN", null);
        store.commit("SET_USERNAME", null);
        this.$router.push({
          name: 'login'
        })
      }
      this.loadPublicRooms();
    },
    components: {
      rooms
    }
  }
</script>
